.TodoSearch {

    background: #F9FBFC;
    border-radius: 2px;
    border: 2px;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1E;
    box-shadow: 0 5px 50px rgba(32, 35, 41, 0.25);

}

.TodoSearch::placeholder {

    color: #a5a5a5;
    font-family: 'Montserrat';
    font-weight: 400;
}

.TodoSearch:focus {

    outline-color: #61DAFA;
}